___CSS_LOADER_EXPORT___.push([module.id, `*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  background: #f9fafb;
  color: #222;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a {
  color: #2563eb;
  text-decoration: none;
  transition: color 0.3s ease;
}
a:hover,
a:focus {
  color: #1d4ed8;
  text-decoration: underline;
}

.fav-btn {
  background: #fffbe6;
  color: #f59e42;
  border: 1px solid #f59e42;
  border-radius: 6px;
  font-weight: bold;
  padding: 0.4rem 1rem;
  cursor: pointer;
  margin-top: 0.5rem;
  transition: background 0.2s, color 0.2s;
}
.fav-btn:hover,
.fav-btn:focus {
  background: #f59e42;
  color: #fff;
}

.skip-link {
  position: absolute;
  left: 0;
  top: -50px; /* Disembunyikan di atas layar alih-alih ke kiri */
  width: auto;
  height: auto;
  padding: 10px 15px;
  background: #000000;
  color: #fff;
  font-weight: bold;
  font-size: 16px;
  text-decoration: none;
  transition: top 0.3s ease;
  z-index: 9999; /* Nilai z-index yang lebih tinggi */
  border-radius: 0 0 8px 0;
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.6);
}

.skip-link:focus {
  top: 0; /* Muncul dari atas ke posisi awal layar */
  left: 0;
  outline: 3px solid #fff; /* Outline tebal untuk lebih jelas */
  outline-offset: 2px;
  text-decoration: none;
}
.header {
  background: #2563eb;
  position: sticky;
  color: white;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  z-index: 100;
  justify-content: space-between;
  flex-wrap: wrap;
  box-shadow: 0 2px 8px rgb(37 99 235 / 0.3);
}
.header h1 {
  margin: 0;
  font-size: 1.8rem;
  font-weight: 700;
  letter-spacing: 1px;
  user-select: none;
}
.nav ul {
  display: flex;
  gap: 1rem;
  margin: 0;
  padding: 0;
  list-style: none;
  align-items: center;
  justify-content: flex-start;
}
.nav ul li a {
  color: white;
  display: flex;
  align-items: center;
  gap: 0.4rem;
  font-weight: 600;
  font-size: 1rem;
  padding: 6px 8px;
  border-radius: 6px;
  transition: background-color 0.3s ease;
}
.nav ul li a:hover,
.nav ul li a:focus {
  background-color: rgba(255 255 255 / 0.2);
  outline: none;
}
.main-content {
  padding: 2rem 1.5rem;
  max-width: 960px;
  margin: 2rem auto;
  border-radius: 12px;
  min-height: 70vh;
  outline: none;
}
.story-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 1.5rem;
}
.story-item {
  background: #f3f4f6;
  border-radius: 12px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  box-shadow: 0 2px 8px rgb(0 0 0 / 0.1);
  transition: transform 0.2s ease;
}
.story-item:hover,
.story-item:focus-within {
  transform: translateY(-4px);
  box-shadow: 0 8px 16px rgb(0 0 0 / 0.15);
}
.story-item img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-bottom: 3px solid #2563eb;
}
.story-content {
  padding: 1rem 1.25rem;
  flex-grow: 1;
}
.story-content h3 {
  margin: 0 0 0.5rem 0;
  font-size: 1.25rem;
  color: #1e40af;
  font-weight: 700;
}
.story-content p {
  margin: 0 0 0.75rem 0;
  font-size: 1rem;
  color: #4b5563;
}
.story-content time {
  font-size: 0.85rem;
  color: #6b7280;
  font-style: italic;
}
#loginLoading {
  display: none;
  margin-top: 1rem;
  font-weight: 700;
  color: #2563eb;
  font-size: 1.1rem;
  text-align: center;
  width: 100%;
}
form {
  background: #f9fafb;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 4px 12px rgb(0 0 0 / 0.07);
  max-width: 540px;
  margin: 0 auto;
  font-size: 1rem;
}
.main-content h2,
form h2 {
  text-align: center;
  color: #000;
  margin-top: 0;
  margin-bottom: 2rem;
  font-size: 2rem;
  font-weight: 700;
  width: 100%;
}
form label {
  display: block;
  margin-top: 1.25rem;
  font-weight: 600;
  color: #374151;
  user-select: none;
}
form input[type="text"],
form input[type="email"],
form input[type="password"],
form textarea,
form input[type="file"] {
  width: 100%;
  padding: 0.65rem 0.85rem;
  margin-top: 0.4rem;
  border-radius: 8px;
  border: 1.8px solid #d1d5db;
  font-size: 1rem;
  color: #111827;
  transition: border-color 0.3s ease;
}
form input[type="text"]:focus,
form input[type="email"]:focus,
form input[type="password"]:focus,
form textarea:focus,
form input[type="file"]:focus {
  outline: none;
  border-color: #2563eb;
  box-shadow: 0 0 6px rgb(37 99 235 / 0.4);
}
form textarea {
  resize: vertical;
  min-height: 100px;
}
form button {
  margin-top: 2rem;
  background: #2563eb;
  color: white;
  border: none;
  padding: 0.9rem 1.8rem;
  font-size: 1.1rem;
  font-weight: 700;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 0 4px 12px rgb(37 99 235 / 0.5);
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}
form button:hover,
form button:focus {
  background: #1d4ed8;
  box-shadow: 0 6px 18px rgb(29 78 216 / 0.7);
  outline: none;
}
#map {
  height: 320px;
  margin-top: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 4px 12px rgb(0 0 0 / 0.1);
  transition: box-shadow 0.3s ease;
}
#map:hover,
#map:focus-within {
  box-shadow: 0 8px 24px rgb(0 0 0 / 0.15);
  outline: none;
}
#formMessage,
#loginMessage,
#registerMessage {
  margin-top: 1rem;
  font-weight: 600;
  font-size: 1rem;
}
@media (max-width: 640px) {
  .header {
    justify-content: center;
  }
  .nav ul {
    flex-direction: column;
    gap: 0.75rem;
    align-items: center;
  }
  .story-list {
    grid-template-columns: 1fr;
  }
  form {
    padding: 1.5rem;
  }
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;;;EAGE,sBAAsB;AACxB;AACA;EACE,4DAA4D;EAC5D,SAAS;EACT,mBAAmB;EACnB,WAAW;EACX,gBAAgB;EAChB,mCAAmC;EACnC,kCAAkC;AACpC;AACA;EACE,cAAc;EACd,qBAAqB;EACrB,2BAA2B;AAC7B;AACA;;EAEE,cAAc;EACd,0BAA0B;AAC5B;;AAEA;EACE,mBAAmB;EACnB,cAAc;EACd,yBAAyB;EACzB,kBAAkB;EAClB,iBAAiB;EACjB,oBAAoB;EACpB,eAAe;EACf,kBAAkB;EAClB,uCAAuC;AACzC;AACA;;EAEE,mBAAmB;EACnB,WAAW;AACb;;AAEA;EACE,kBAAkB;EAClB,OAAO;EACP,UAAU,EAAE,kDAAkD;EAC9D,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,mBAAmB;EACnB,WAAW;EACX,iBAAiB;EACjB,eAAe;EACf,qBAAqB;EACrB,yBAAyB;EACzB,aAAa,EAAE,oCAAoC;EACnD,wBAAwB;EACxB,6CAA6C;AAC/C;;AAEA;EACE,MAAM,EAAE,0CAA0C;EAClD,OAAO;EACP,uBAAuB,EAAE,oCAAoC;EAC7D,mBAAmB;EACnB,qBAAqB;AACvB;AACA;EACE,mBAAmB;EACnB,gBAAgB;EAChB,YAAY;EACZ,kBAAkB;EAClB,aAAa;EACb,mBAAmB;EACnB,YAAY;EACZ,8BAA8B;EAC9B,eAAe;EACf,0CAA0C;AAC5C;AACA;EACE,SAAS;EACT,iBAAiB;EACjB,gBAAgB;EAChB,mBAAmB;EACnB,iBAAiB;AACnB;AACA;EACE,aAAa;EACb,SAAS;EACT,SAAS;EACT,UAAU;EACV,gBAAgB;EAChB,mBAAmB;EACnB,2BAA2B;AAC7B;AACA;EACE,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,WAAW;EACX,gBAAgB;EAChB,eAAe;EACf,gBAAgB;EAChB,kBAAkB;EAClB,sCAAsC;AACxC;AACA;;EAEE,yCAAyC;EACzC,aAAa;AACf;AACA;EACE,oBAAoB;EACpB,gBAAgB;EAChB,iBAAiB;EACjB,mBAAmB;EACnB,gBAAgB;EAChB,aAAa;AACf;AACA;EACE,gBAAgB;EAChB,UAAU;EACV,SAAS;EACT,aAAa;EACb,2DAA2D;EAC3D,WAAW;AACb;AACA;EACE,mBAAmB;EACnB,mBAAmB;EACnB,gBAAgB;EAChB,aAAa;EACb,sBAAsB;EACtB,sCAAsC;EACtC,+BAA+B;AACjC;AACA;;EAEE,2BAA2B;EAC3B,wCAAwC;AAC1C;AACA;EACE,WAAW;EACX,aAAa;EACb,iBAAiB;EACjB,gCAAgC;AAClC;AACA;EACE,qBAAqB;EACrB,YAAY;AACd;AACA;EACE,oBAAoB;EACpB,kBAAkB;EAClB,cAAc;EACd,gBAAgB;AAClB;AACA;EACE,qBAAqB;EACrB,eAAe;EACf,cAAc;AAChB;AACA;EACE,kBAAkB;EAClB,cAAc;EACd,kBAAkB;AACpB;AACA;EACE,aAAa;EACb,gBAAgB;EAChB,gBAAgB;EAChB,cAAc;EACd,iBAAiB;EACjB,kBAAkB;EAClB,WAAW;AACb;AACA;EACE,mBAAmB;EACnB,aAAa;EACb,mBAAmB;EACnB,wCAAwC;EACxC,gBAAgB;EAChB,cAAc;EACd,eAAe;AACjB;AACA;;EAEE,kBAAkB;EAClB,WAAW;EACX,aAAa;EACb,mBAAmB;EACnB,eAAe;EACf,gBAAgB;EAChB,WAAW;AACb;AACA;EAC